import React from "react";
import { LinearProgress, Grid } from "@material-ui/core";

import FileHeader from "./FileHeader";

function SingleFileUpload({ file, onDelete }) {
  return (
    <Grid item>
      <FileHeader file={file} onDelete={onDelete} disableRemoveButton={false} />
      {/* {disableRemoveButton && <LinearProgress variant="determinate" />} */}
    </Grid>
  );
}

export default SingleFileUpload;
